<template>
  <div class="home">
    <div class="header">
      <div class="address-map" @click="$router.push({name:'address',params:{city}})">
        <i class="fa fa-map-marker"></i>
        <span>{{address}}</span>
        <i class="fa fa-sort-desc"></i>
      </div>
      <div class="shop-search">
        <i class="fa fa-search"></i>
        <input type="text" placeholder="搜索商家 商家名称">
      </div>
    </div>
    <loading v-if="loading"></loading>
  </div>
</template>

<script>
import Loading from '../components/Loading'
export default {
  name: 'home',
  components: {
    Loading
  },
  computed: {
    loading () {
      return this.$store.getters.loading
    },
    address () {
      return this.$store.getters.address
    },
    city () {
      return (
        this.$store.getters.location.addressComponent.city ||
        this.$store.getters.location.addressComponent.province
      )
    }
  }
  // beforeRouteEnter (to, from, next) {
  //   next(vm => {
  //     vm.address = '..ssfsfs...'
  //   })
  // }
}
</script>

<style scoped>
.home {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
}
.header {
  background-color: #009eef;
  padding: 16px;
}
.header .address-map {
  color: #fff;
  font-weight: bold;
}
.address-map i:nth-child(1) {
  margin: 0 8px;
  font-size: 18px;
}
.address-map span {
  display: inline-block;
  /* width: 80%; */
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 10px;
}
.header .shop-search {
  margin-top: 10px;
  background-color: #fff;
  padding: 10px 0;
  border-radius: 4px;
  text-align: center;
  color: #aaa;
}
.shop-search input {
  width: 80%;
  height: 80%;
  outline: none;
  border: none;
  margin-left: 5px;
}
</style>
